.page{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.array-container{
    left: 2vw;
    top: 2vh;
    display: flex;
    align-items: center;
}

.array-bar{
    width: 0.65vw;
    background-color: #51a0a0;
    margin: 0 1.1px;
}

button{
    margin: 10px 20px;
    border-radius: 0.5rem;
    background-color: #51a0a0;
    color: rgb(11, 11, 11);
    font-weight: bolder;
}
button:hover{
    background-color: rgb(88, 88, 88);
    color: black;
}